import React, { createContext, useContext, useEffect, useState } from 'react';
import Cookies from 'js-cookie';
import toast from 'react-hot-toast';
import { useNavigate } from 'react-router-dom'; 

const initUserData = {
  id: 0,
  email: '',
  name: '',
};

const ProUser = {
  id: 1,
  email: '123@test.com',
  password: '123456',
  name: '',
};

// Context
const AuthContext = createContext(null);

export function AuthProvider({ children }) {
 
  // 
  const [auth, setAuth] = useState({
    isAuth: false,
    userData: initUserData,
  });
  const navigate = useNavigate();

  // 
  const handleLogout = () => {
    try {
      // cookie
      Cookies.remove('authData'); //  cookie

      // 
      const nextAuth = {
        isAuth: false,
        userData: initUserData,
      };

      setAuth(nextAuth);

      toast.success('');
    } catch (e) {
      console.error(e);
    }
  };

  // cookie
  const handleLogin = (email, password) => {
    try {
      // 
      if (password === ProUser.password && email === ProUser.email) {
        //  token  cookie
        const authData = {
          token: 'fake-jwt-token', //  token
          userData: {
            id: ProUser.id,
            email: ProUser.email,
            name: ProUser.name,
          },        // 
        };
        Cookies.set('authData', JSON.stringify(authData), { expires: 1 });

        // 
        const nextAuth = {
          isAuth: true,
          userData: {
            id: ProUser.id,
            email: ProUser.email,
            name: ProUser.name,
          }, 
        };

        setAuth(nextAuth);

        toast.success('');

        navigate('/');
      } else {
        toast.error('');
      }
    } catch (e) {
      console.error(e);
    }
  };

  // cookietoken
  const getAuthHeader = () => {
    const authData = Cookies.get('authData');
    if (authData) {
      const parsedAuthData = JSON.parse(authData);
      return {
        Authorization: `Bearer ${parsedAuthData.token}`,
      };
    }
    return {};
  };

  useEffect(() => {
    // cookietoken
    const authData = Cookies.get('authData');
    if (authData) {
      const parsedAuthData = JSON.parse(authData);
      setAuth({
        isAuth: true,
        userData: parsedAuthData.userData, // 
      });
    }
  }, []);

  return (
    <AuthContext.Provider
      value={{
        auth,
        setAuth,
        handleLogin,
        handleLogout,
        getAuthHeader,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

// useAuth
export const useAuth = () => useContext(AuthContext);
