import React, { useState, useEffect } from 'react';
import { TextField, Button, IconButton, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Modal, Box, Grid2 } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import dayjs from 'dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import useMediaQuery from '@mui/material/useMediaQuery'; 
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

const ToDoList = () => {
  const [todos, setTodos] = useState([]);
  const [newTodo, setNewTodo] = useState('');
  const [editTodoId, setEditTodoId] = useState(null);
  const [editText, setEditText] = useState('');
  const [filterDate, setFilterDate] = useState(null);
  const [openAddModal, setOpenAddModal] = useState(false);
  const [openDeleteModal, setOpenDeleteModal] = useState(false);
  const [deleteTodoId, setDeleteTodoId] = useState(null);
  const isSmallScreen = useMediaQuery('(max-width:600px)');
  //  sessionStorage 
  useEffect(() => {
    const storedTodos = sessionStorage.getItem('todos');
    if (storedTodos) {
      setTodos(JSON.parse(storedTodos));
    }
  }, []);

  //  todos  sessionStorage
  useEffect(() => {
    sessionStorage.setItem('todos', JSON.stringify(todos));
  }, [todos]);

  // 
  const handleAddTodo = () => {
    if (newTodo.trim() === '') return;
    const newTask = {
      id: Date.now(),
      text: newTodo,
      date: dayjs().format('YYYY-MM-DD HH:mm:ss'),
    };
    setTodos([...todos, newTask]);
    setNewTodo('');
    setOpenAddModal(false); //  Modal
  };

  // 
  const handleDelete = () => {
    setTodos(todos.filter((todo) => todo.id !== deleteTodoId));
    setOpenDeleteModal(false); //  Modal
  };

  // 
  const handleEdit = (id) => {
    const todoToEdit = todos.find((todo) => todo.id === id);
    setEditTodoId(id);
    setEditText(todoToEdit.text);
  };

  // 
  const handleUpdate = () => {
    setTodos(
      todos.map((todo) =>
        todo.id === editTodoId ? { ...todo, text: editText } : todo
      )
    );
    setEditTodoId(null);
    setEditText('');
  };

  // 
  const filteredTodos = filterDate
    ? todos.filter((todo) =>
        dayjs(todo.date).isSame(dayjs(filterDate), 'day')
      )
    : todos;

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>

      <section>
      
        
        {/* DateTimePicker for filtering */}
        <DateTimePicker
          label=""
          value={filterDate}
          onChange={(newValue) => setFilterDate(newValue)}
          renderInput={(params) => <TextField {...params} sx={{ mt: 2, mb: 2 }} />}
        />

        {/*  To-Do List */}
        <Grid2 container spacing={2}>
      <TableContainer component={Paper}>
   
        {isSmallScreen ? (
          // 
          <Grid2 container spacing={2}>
            {filteredTodos.map((todo) => (
              <Grid2 item xs={12} key={todo.id}>
                <Paper sx={{ p: 2 }}>
                  <Grid2 container spacing={1}>
                    <Grid2 item xs={12}>
                      <strong></strong> {todo.text}
                    </Grid2>
                    <Grid2 item xs={12}>
                      <strong></strong> {todo.date}
                    </Grid2>
                    <Grid2 item xs={12} sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                      <IconButton onClick={() => handleEdit(todo.id)}>
                        <EditIcon />
                      </IconButton>
                      <IconButton onClick={() => { setDeleteTodoId(todo.id); setOpenDeleteModal(true); }}>
                        <DeleteIcon />
                      </IconButton>
                    </Grid2>
                  </Grid2>
                </Paper>
              </Grid2>
            ))}
          </Grid2>
        ) : (
          // 
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell align="right"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {filteredTodos.map((todo) => (
                <TableRow key={todo.id}>
                  <TableCell component="th" scope="row">
                    {todo.text}
                  </TableCell>
                  <TableCell align="right">{todo.date}</TableCell>
                  <TableCell align="right">
                    <IconButton onClick={() => handleEdit(todo.id)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton onClick={() => { setDeleteTodoId(todo.id); setOpenDeleteModal(true); }}>
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}
      </TableContainer>
    </Grid2>
  
    

        {/*  */}
        <Button variant="contained" startIcon={<AddIcon />} sx={{ mt: 2 }} onClick={() => setOpenAddModal(true)}>
          
        </Button>

        {/*  Modal */}
        <Modal
          open={openAddModal}
          onClose={() => setOpenAddModal(false)}
          aria-labelledby="add-todo-modal"
          aria-describedby="add-todo-description"
        >
          <Box sx={style}>
            <h2 id="add-todo-modal"></h2>
            <TextField
              label=""
              value={newTodo}
              onChange={(e) => setNewTodo(e.target.value)}
              fullWidth
            />
            <Button variant="contained" sx={{ mt: 2 }} onClick={handleAddTodo}>
              
            </Button>
          </Box>
        </Modal>

        {/*  Modal */}
        <Modal
          open={openDeleteModal}
          onClose={() => setOpenDeleteModal(false)}
          aria-labelledby="delete-todo-modal"
          aria-describedby="delete-todo-description"
        >
          <Box sx={style}>
            <h2 id="delete-todo-modal"></h2>
            <p></p>
            <Button variant="contained" sx={{ mt: 2 }} color="error" onClick={handleDelete}>
              
            </Button>
          </Box>
        </Modal>
      </section>
    </LocalizationProvider>
  );
};

export default ToDoList;
